<script src="../../../config/index.js"></script>
<template>
  <div class="clearfix">
    <div class="operation">
      <div class="clearfix ">
        <el-form label-width="100px">
          <el-col :span="5">
            <el-form-item label="配送员名称">
              <el-input v-model="tempData.courierName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="所属店铺" style="position: relative">
              <el-input
                v-model="tempData.shopName"
                placeholder="输入所属店铺">
              </el-input>
              <div class="el-select-dropdown el-popper" v-show="this.tempData.shopName" style="min-width: 85.2031px; position: absolute; top: 100%; left: 0px; transform-origin: center top 0px; z-index: 2005;" x-placement="bottom-start">
                <div class="el-scrollbar" style="">
                  <div class="el-select-dropdown__wrap el-scrollbar__wrap" style="margin-bottom: -17px; margin-right: -17px;">
                    <ul class="el-scrollbar__view el-select-dropdown__list">
                      <template v-for="item in categoryList">
                        <li class="el-select-dropdown__item" @click="setItem(item.shopName)"><span>{{item.shopName}}</span></li>
                      </template>
                    </ul>
                  </div>
                  <div class="el-scrollbar__bar is-horizontal">
                    <div class="el-scrollbar__thumb" style="transform: translateX(0%);"></div>
                  </div>
                  <div class="el-scrollbar__bar is-vertical">
                    <div class="el-scrollbar__thumb" style="transform: translateY(0%);"></div>
                  </div>
                </div><!---->
                <div x-arrow="" class="popper__arrow" style="left: 35px;"></div>
              </div>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item label="审核">
              <el-select @change="selectStatus" v-model="tempData.isAudit" placeholder="请选择审核">
                <el-option label="默认" value=""></el-option>
                <el-option label="审核" value="1"></el-option>
                <el-option label="审核通过" value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="3" style="padding-left:40px">
            <el-button type="primary" @click="search" icon="el-icon-search">查询</el-button>
          </el-col>
          <el-col :span="1" style="padding-left:20px">
            <el-button type="primary" @click="editorDia('add')">新增</el-button>
          </el-col>
        </el-form>
      </div>
    </div>
    <div class="table">
      <el-table
        border
        :data="dataList"
        style="width: 100%;margin-top:20px;"
      >
        <el-table-column
          fixed
          label="序号"
          width="80"
          align="center"
          type="index" >
        </el-table-column>
        <el-table-column
          prop="count"
          label="总单数"
          width="200"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="courierName"
          label="配送员名"
          width="200"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="申请时间"
          width="200"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="email"
          label="邮箱"
          width="200"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="latitude"
          label="纬度"
          width="200"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="longitude"
          label="经度"
          width="200"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="phone"
          label="手机号"
          width="220"
          align="center" >
        </el-table-column>
        <el-table-column
          prop="shopName"
          label="店铺名"
          width="250"
          align="center" >
        </el-table-column>
        <el-table-column label="操作" fixed="right" align="center" width="200">
          <template slot-scope="scope">
            <el-button type="primary" @click="editorDia('update', scope.row)">查看当前位置</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <pagination
      :current-page="page.currentPage"
      :page-size="page.pageSize"
      :total="page.pageNum"
      @sizeChange="sizeChange"
      @currentChange="currentChange"
    >
    </pagination>
    <el-dialog title="审核" :visible.sync="editorVisable" width="20%" center="true">
      <el-form >
        <el-form-item label="审核">
          <el-select v-model="editorItem.isAudit" placeholder="请选择审核">
            <el-option label="默认" value=""></el-option>
            <el-option label="审核通过" value="2"></el-option>
            <el-option label="审核失败" value="1"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editorVisable = false">取 消</el-button>
        <el-button type="primary" @click="updateExamine">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="冻结" :visible.sync="frozen" width="20%" center="true">
      <el-form >
        <el-form-item label="冻结">
          <el-select v-model="editorItem2.isDisable" placeholder="请选择是否冻结">
            <el-option label="默认" value=""></el-option>
            <el-option label="冻结" value="2"></el-option>
            <el-option label="解冻" value="1"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editorVisable = false">取 消</el-button>
        <el-button type="primary" @click="updateFrozen">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="新增"
      :visible.sync="addStatus"
      width="550px"
      center
      :modal-append-to-body="false"
    >
      <el-form :model="operTreeData" ref="operTreeData">
        <el-form-item label="配送员名">
          <el-input v-model="addData.courierName">
          </el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="addData.email"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="addData.password"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="addData.phone"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="add" type="success">提交</el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="编辑"
      :visible.sync="updateStatus"
      width="550px"
      center
      :modal-append-to-body="false"
    >
      <el-form :model="operTreeData" ref="operTreeData">
        <el-form-item label="配送员名">
          <el-input v-model="updateData.courierName">
          </el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="updateData.email"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="updateData.password"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="updateData.phone"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="update" type="success">提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import { managerCourierStatistical, managerClShopName } from "@/api/index.js";
  import pagination from "@/components/pagination";
  export default {
    components: {
      pagination
    },
    data() {
      return {
        dataList: [],
        //搜索时的临时数据
        tempData: {
          courierName: '',
          shopName: '',
          isAudit: ''
        },
        frozen: false,
        editorVisable: false,
        addStatus: false,
        editorItem: {
          isAudit: ''
        },
        categoryList: [],
        editorItem2: {
          isDisable: '',
        },
        addData: {
          courierName: '',
          email: '',
          password: '',
          phone: ''
        },
        updateData: {},
        updateStatus: false,
        //搜索时的数据
        serData: {},
        //分页信息
        page: {
          pageNum: 10,
          currentPage: 1,
          pageSize: 10
        }
      };
    },
    watch:{
      'tempData.shopName' (val) {
        managerClShopName({
          page:1,
          size:50,
          shopName:val
        }).then((res)=>{
          console.log(res);
          this.categoryList=res
        })
      }
    },
    methods: {
      //加载数据
      initData(page, size) {
        console.log(1);
        // getAreaTree().then(res => {
        //   console.log(res)
        // })
        managerCourierStatistical({
          page: page || this.page.currentPage,
          size: size || this.page.pageSize,
          courierName: this.tempData.courierName,
          shopName: this.tempData.shopName
        }).then(res => {
          this.dataList = res.list;
          this.page.pageNum = res.total_record;
        });
      },
      setItem(name) {
        this.tempData.shopName = name
      },
      editorDia(type, row){
        if (type == 'update') {
          this.updateData = row;
          this.updateStatus = true;
        }else {
          this.addStatus = true
        }
      },
      add() {
        managerAdd(this.addData).then( res => {
          this.addStatus = false;
          this.initData(1);
        } )
      },
      update() {
        mUpdate(this.updateData).then(res => {
          this.updateStatus = false;
          this.initData(1);
        })
      },
      editor(id){
        console.log('---->', id);
        this.editorVisable=true;
        this.editorItem['iId'] = id;
        // this.editorItem=obj;
      },
      frozenUpdate(id){
        console.log('---->', id);
        this.frozen=true;
        this.editorItem2['iId'] = id;
        // this.editorItem=obj;
      },
      updateExamine() {
        managerUpdateIsAudit(this.editorItem).then( res => {
          this.editorVisable=false;
          this.initData(1);
        })
      },
      //删除商品
      del(id) {
        this.$confirm('此操作将永久删除该配送员信息,是否继续?', '警告', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(()=>{
          managerUpdateIsDelete({
            'iId': id,
            'isDelete': 2
          }).then(res=>{
            this.initData(1)
          })
        })
      },
      updateFrozen() {
        managerUpdateIsDisable(this.editorItem2).then( res => {
          this.frozen=false;
          this.initData(1);
        })
      },
      //页数改变时的回调
      currentChange(val) {
        this.page.currentPage = val;
        this.initData();
      },
      //每页总数改变时的回调
      sizeChange(val) {
        this.page.pageSize = val;
        this.initData(1);
      },
      selectStatus() {
        this.initData(1);
      },
      //条件筛选店铺
      search() {
        this.initData(1);
      }
    },
    beforeMount() {
      this.initData();
    }
  };
</script>

<style>
  .addBatchBtn a {
    color: #ffffff;
  }
  .m-select {

  }
  .content {
    /*min-width: 1200px;*/
    background: rgb(255, 255, 255);
    padding: 26px 30px;
    position: relative;
    /*min-height: 800px;*/
  }
  h4 {
    text-align: left;
    color: #333333;
    font-size: 18px;
    font-family: PingFang-SC-Bold;
    padding-bottom: 25px;
    border-bottom: 1px solid #e1e1e1;
  }
</style>



